/**
 * @refer https://github.com/GoogleChromeLabs/browser-fs-access#opening-files
 */
import { fileOpen, fileSave } from "browser-fs-access";
import type { FileWithHandle } from "browser-fs-access";
import { useCallback, useEffect, useRef, useState } from "react";

type UploaderOptions = {
  [x in string]: string;
};

const uploaderOptions: UploaderOptions = {
  accept: "image/*",
};

/**
 *  input[type=file] 
 * @param options  {accept: "image/*"}
 * @returns
 */
export const useUploader = (options: UploaderOptions = uploaderOptions) => {
  const [blob, setBlob] = useState<FileWithHandle>();
  const [src, setSrc] = useState<string>("");
  const input = useRef<HTMLInputElement | null>(null);

  useEffect(() => {
    input.current = document.createElement("input");
    Object.keys(options).forEach((key) => {
      if (input.current) {
        (input.current as any)[key] = options[key];
      }
    });
    input.current.type = "file";
    input.current.onchange = (e) => {
      const files = (e.target as HTMLInputElement).files;

      if (files && files.length) {
        const blob = files[0];
        const localUrl = URL.createObjectURL(blob); //  Blod url
        setSrc(localUrl);
        setBlob(files[0]);
      }
    };
    return () => {
      input.current = null;
    };
  });

  const openFile = useCallback(async () => {
    try {
      input.current?.click();
    } catch (_e) {}
  }, []);

  return { blob, src, openFile };
};

/**
 * 
 * @param canvas
 * @param blob blob
 * @param extensions 
 * @returns
 */
export const useDownloader = (
  canvas?: HTMLCanvasElement | null,
  blob?: FileWithHandle,
  extensions: string[] = [".png"]
) => {
  const [name, setName] = useState("");

  useEffect(() => {
    setName(`barba_${blob?.name}`);
  }, [blob]);

  const downloadFile = async () => {
    if (!canvas) {
      return;
    }
    canvas.toBlob(async (canvasBlob) => {
      if (!canvasBlob) {
        return;
      }

      await fileSave(canvasBlob, {
        fileName: name,
        extensions: extensions,
      });
    });
  };
  return { name, downloadFile };
};

type FileUploaderOptions = GetType<typeof fileOpen>;

const imageOptions = {
  mimeTypes: ["image/*"],
};

/**
 *  GoogleChromeLabs/browser-fs-access API
 * @param options
 * @returns
 */
export const useFileUploader = (
  options: FileUploaderOptions = imageOptions
) => {
  const [blob, setBlob] = useState<FileWithHandle>();
  const [src, setSrc] = useState<string>("");

  const openFile = useCallback(async () => {
    try {
      const blob = (await fileOpen({
        ...options,
        multiple: false,
      })) as FileWithHandle;

      if (blob) {
        const localUrl = URL.createObjectURL(blob); //  Blod url
        setSrc(localUrl);
        setBlob(blob);
      }
    } catch (_e) {}
  }, [options]);

  const onSaveFile = useCallback(async () => {
    if (!blob) {
      return;
    }
    await fileSave(blob, {
      fileName: blob?.name,
      extensions: [".png"],
    });
  }, [blob]);

  return { blob, src, openFile, onSaveFile };
};
